
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { FileSize } from "@/shared/services/FileSizeService";
import { FILE, FILE_TYPE } from '@/shared/constants/common.const';
import { AttachInfoList } from "@/shared/type/ICM21100112.type";
import { modalController } from "@ionic/vue";
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import { screenMode } from '@/shared/type/Approval.type';
import { FileInfo } from '@/shared/type/ICM20100112.type';
import TextEditor from '@/shared/components/TextEditor/TextEditor.vue';
import { ModifyCardType } from '@/shared/components/ModifyCard/ModifyCard.type';
import { Guide_Inquiry_Details, ICM20600112_REQ_BODY, ICM20600112_RES } from '@/shared/type/ICM20600112.type';
import { ContentList, Guide_Step_Detail_Inquiry_List, ICM20600113_REQ_BODY, ICM20600113_RES } from '@/shared/type/ICM20600113.type';
import { DataCenter } from '@/shared/services/DataCenter';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import draggable from "vuedraggable";

const fileSize = new FileSize();

export default defineComponent({
    name: "icm206001sc01",

    components: {
        baseTab,
        basePan,
        VueDatePicker,
        ModifyCard,
        BreadCrumb,
        SelectOption,
        TextEditor,
        draggable
    },

    data() {
        return {
            headersIcon: [
                { key: "En", name: "common.english" },
                { key: "Km", name: "common.khmer" },
                { key: "Ko", name: "common.korean" },
                // { key: "Ja", name: "common.japanese" },
                // { key: "Zh", name: "common.chinese" },
            ],
            header: [
                { columnName: this.$t('Step No.') },
                { columnName: this.$t('Title') },
                { columnName: 'Contents' },
                { columnName: this.$t('Image File') },
                { columnName: this.$t('Status') },
                { columnName: this.$t('Uploader') },
                { columnName: this.$t('Create date') }
            ],
            guideDetailsLangDefaultValue: [
                { languageCode: "common.english", title: "", guideContent: "", guideContentHtml: "" },
                { languageCode: "common.khmer", title: "", guideContent: "", guideConttml: "" },
                { languageCode: "common.korean", title: "", guideContent: "", guideContentHtml: "" },
                { languageCode: "common.japanese", title: "", guideContent: "", guideContentHml: "" },
                { languageCode: "common.chinese", title: "", guideContent: "", guideContentHtml: "" },
            ] as ContentList[],
            guideDetail: {
                attachInfoList: [] as AttachInfoList[],
                deleteBranchCode: '',
                deleteDate: '',
                deleteTellerID: '',
                deleteTellerName: '',
                deleteTime: '',
                firstRegisterBranchCode: '',
                firstRegisterDate: '',
                lastChangeTellerID: '',
                lastChangeTellerName: '',
                lastChangeTime: '',
                statusCode: '',
                refScreenID: '',
                guideID: '',
                displayYN: '',
            } as Guide_Inquiry_Details,
            guideStepList: [] as Guide_Step_Detail_Inquiry_List[],
            fileIdListForDelete: [] as { fileId: string }[],
            isDisable: true,
            isInValidInput: false,
            fileInfo: {} as FileInfo,
            getImage: null as any,
            getFile: null,
            modifyCard: {} as ModifyCardType,
            isFileEmty: false,
            isImageEmty: false,
            isGuideEdit: false,
            isEditFormData: true,
            isItemDeleted: false,
            isExceed: false,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        };
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        btnDelete() {
            if (this.$route.query.type === 'edit' && this.isDisable) {
                return true
            }
            return false
        },

        btnList() {
            if (this.$route.query.type === 'edit' && this.isDisable) {
                return true
            }
            return false
        },

        btnCancel() {
            if (!this.isDisable) {
                return true
            }
            return false
        },

        btnEdit() {
            if (this.$route.query.type === 'edit' && this.isDisable) {
                return true
            }
            return false
        },

        btnClear(): boolean {
            const isThereTrue = this.guideStepList.find((item) => item.isChecked)
            if (isThereTrue && !this.isDisable && !this.isGuideEdit) {
                return true;
            }
            return false;
        },

        btnSave() {
            if ((this.$route.query.type == 'edit' && !this.isDisable) || (this.$route.query.type == 'register')) {
                return true
            }
            return false
        },

        btnRemove(): boolean {
            const isThereTrue = this.guideStepList.find((item) => item.isChecked)
            if (!isThereTrue) {
                return true;
            }
            return false;
        },

        btnFeeEdit() {
            const isThereTrue = this.guideStepList.find((item) => item.isChecked)
            if (!this.isDisable && !this.isGuideEdit && !isThereTrue) {
                return true
            }
            return false
        },

        btnFeeSave() {
            const isThereTrue = this.guideStepList.find((item) => item.isChecked)
            if (this.isDisable && this.isGuideEdit && !isThereTrue) {
                return true
            }
            return false
        },

        btnAdd(): boolean {
            const isThereTrue = this.guideStepList.find((item) => item.isChecked)
            if (isThereTrue) {
                return true;
            }
            return false;
        },

        isItemChecked(): boolean {
            const isChecked = this.guideStepList.some(item => item.isChecked)
            return isChecked
        },


    },

    async mounted() {
        if (this.approvalType === '') {
            // this.$util.editableContent(false)
            if (this.$route.query.type == "register") {
                this.$util.editableContent(true)
                this.isDisable = false
            } else {
                this.$util.editableContent(false)
                this.requestGuideDetail()
                this.requestGuideStepDetailInquiryList()
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const reqBody = { guideID: Number(authDetailParsed?.guideID) } as ICM20600112_REQ_BODY
                    const res = await this.$request.getRequest("ICM20600112", reqBody, false) as ICM20600112_RES

                    const reqBodyList = { guideID: Number(authDetailParsed?.guideID) } as ICM20600113_REQ_BODY
                    const resList = await this.$request.getRequest("ICM20600113", reqBodyList, false) as ICM20600113_RES
                    this.$util.editableContent(false);
                    //   await this.assignValueToTab(this.convertCommonCode(res?.body.contentListList))
                    this.guideDetail = {
                        displayYN: res?.body.displayYN,
                        statusCode: res?.body.statusCode,
                        guideID: res?.body.guideID,
                        refScreenID: res?.body.refScreenID,
                        attachInfoList: res?.body.attachInfoList
                    }
                    this.guideStepList = resList.body.list.map((item: Guide_Step_Detail_Inquiry_List) => {
                        return {
                            attachTypeCode: item.attachTypeCode,
                            contentList: this.convertCommonCode(item.contentList) as ContentList[],
                            fileId: item.fileId,
                            filePath: item.filePath,
                            firstRegisterDate: item.firstRegisterDate,
                            firstRegisterTellerID: item.firstRegisterTellerID,
                            firstRegisterTellerName: item.firstRegisterTellerName,
                            firstRegisterTime: item.firstRegisterTime,
                            guideID: item.guideID,
                            seqNo: item.seqNo,
                            statusCode: item.statusCode,
                            title: item.title,
                            fileName: item.fileName
                        }
                    })
                    await this.requestFileDetail()
                } else {
                    //   await this.assignValueToTab(this.convertCommonCode(authDetailParsed.contentListList))
                    this.guideDetail = {
                        displayYN: authDetailParsed?.displayYN,
                        statusCode: authDetailParsed?.statusCode,
                        guideID: authDetailParsed?.guideID,
                        refScreenID: authDetailParsed?.refScreenID,
                        attachInfoList: authDetailParsed?.attachInfoList
                    }
                    this.guideStepList = authDetailParsed?.guideStepList.map((item: Guide_Step_Detail_Inquiry_List) => {
                        return {
                            attachTypeCode: item.attachTypeCode,
                            contentList: this.convertCommonCode(item.contentList) as ContentList[],
                            fileId: item.fileId,
                            filePath: item.filePath,
                            firstRegisterDate: item.firstRegisterDate,
                            firstRegisterTellerID: item.firstRegisterTellerID,
                            firstRegisterTellerName: item.firstRegisterTellerName,
                            firstRegisterTime: item.firstRegisterTime,
                            guideID: item.guideID,
                            seqNo: item.seqNo,
                            statusCode: item.statusCode,
                            title: item.title,
                            fileName: item.fileName
                        }
                    })
                    await this.requestFileDetail()
                }
            })
        }
    },

    watch: {
        guideDetailsLangDefaultValue: {
            handler(value: ContentList[]) {
                value.forEach((item: { guideContent: string; guideContentHtml: string; }) => {
                    item.guideContent = this.$validate.formatHtmlToString(item.guideContentHtml)
                })
            },
            deep: true
        }
    },


    methods: {
        async requestGuideDetail() {
            const reqBody = { guideID: Number(this.$route.query.guideID) } as ICM20600112_REQ_BODY
            const res = await this.$request.getRequest("ICM20600112", reqBody, false) as ICM20600112_RES
            if (res.header.result) {
                this.guideDetail = {
                    displayYN: res?.body.displayYN,
                    statusCode: res?.body.statusCode,
                    guideID: res?.body.guideID,
                    refScreenID: res?.body.refScreenID,
                    attachInfoList: res?.body.attachInfoList
                }
                await this.requestFileDetail()
            }
        },

        async requestGuideStepDetailInquiryList() {
            const reqBody = { guideID: Number(this.$route.query.guideID) } as ICM20600113_REQ_BODY
            const res = await this.$request.getRequest("ICM20600113", reqBody, false) as ICM20600113_RES
            if (res.header.result) {
                this.guideStepList = res.body.list.map((item: Guide_Step_Detail_Inquiry_List) => {
                    return {
                        attachTypeCode: item.attachTypeCode,
                        contentList: this.convertCommonCode(item.contentList) as ContentList[],
                        fileId: item.fileId,
                        filePath: item.filePath,
                        firstRegisterDate: item.firstRegisterDate,
                        firstRegisterTellerID: item.firstRegisterTellerID,
                        firstRegisterTellerName: item.firstRegisterTellerName,
                        firstRegisterTime: item.firstRegisterTime,
                        guideID: item.guideID,
                        seqNo: item.seqNo,
                        statusCode: item.statusCode,
                        fileName: item.fileName,
                        title: item.title,
                        image: [],
                        savedYN: 'N',
                        removeImageYN: 'N'
                    }
                })
            }
        },

        async requestFileDetail() {
            for (let i = 0; i < this.guideDetail.attachInfoList.length; i++) {
                if (this.guideDetail.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
                    const res = await this.$file.getFileInfo(this.guideDetail.attachInfoList[i].fileId)
                    this.fileInfo.fileName = res?.data.body.originalFileName
                    this.fileInfo.fileSize = res?.data.body.fileSizeString
                }
            }
        },

        async requestImageDetail(fileId: string) {
            const res = await this.$file.getFileInfo(fileId)
            this.fileInfo.imageName = res?.data.body.originalFileName
            this.fileInfo.imgSize = res?.data.body.fileSizeString
            const resImage = await this.$file.previewImage(fileId)
            this.fileInfo.image = resImage
        },

        async handleRegister() {
            await this.handleFileRegister().then(async () => {
                const guideStepList: Guide_Step_Detail_Inquiry_List[] = this.guideStepList.map((item: Guide_Step_Detail_Inquiry_List, index: number) => {
                    return {
                        contentList: this.convertLangToCode(JSON.parse(JSON.stringify(item.contentList))),
                        attachTypeCode: FILE_TYPE.IMAGE,
                        fileId: item.fileId,
                        filePath: item.filePath,
                        seqNo: index + 1,
                        fileName: item.fileName
                    }
                })
                const reqBody = {
                    displayYN: this.guideDetail.displayYN,
                    guideStepList: guideStepList,
                    attachInfoList: this.guideDetail.attachInfoList,
                    refScreenID: this.guideDetail.refScreenID
                }


                const res = await this.$request.getRequest("ICM20600121", reqBody, false) as any
                if (res.header.result) {
                    this.$router.push("ICM206001SC00");
                    this.$dialog.onOpen({
                        content: this.$t("message.register_success"),
                        dialogMode: 'Success',
                        callback: () => {
                            modalController.dismiss()
                        }
                    });
                }
            })
        },

        async handleUpdate() {
            await this.handleFileUpdate().then(async () => {
                const guideStepList: Guide_Step_Detail_Inquiry_List[] = this.guideStepList.map((item: Guide_Step_Detail_Inquiry_List, index: number) => {
                    return {
                        contentList: this.convertLangToCode(JSON.parse(JSON.stringify(item.contentList))),
                        attachTypeCode: FILE_TYPE.IMAGE,
                        fileId: item.fileId,
                        filePath: item.filePath,
                        seqNo: index + 1,
                        statusCode: item.statusCode,
                        fileName: item.fileName
                    }
                })
                const reqBody = {
                    guideID: Number(this.$route.query.guideID),
                    displayYN: this.guideDetail.displayYN,
                    guideStepList: guideStepList,
                    attachInfoList: this.guideDetail.attachInfoList,
                    refScreenID: this.guideDetail.refScreenID
                }
                const res = await this.$request.getRequest("ICM20600131", reqBody, false) as any
                if (res.header.result) {
                    this.isDisable = true;
                    this.$dialog.onOpen({
                        content: this.$t("message.update_success"),
                        dialogMode: 'Success',
                        callback: () => {
                            this.requestGuideDetail()
                            this.requestGuideStepDetailInquiryList()
                            modalController.dismiss()
                        }
                    });
                }
            })
        },

        async handleDelete() {
            const reqBody = { guideID: Number(this.$route.query.guideID) } as any
            const res = await this.$request.getRequest("ICM20600141", reqBody, false) as any

            if (res.header.result) {
                this.$router.push("ICM206001SC00")
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        async handleAdd() {
            const item: any = this.guideDetailsLangDefaultValue.find(data => data.languageCode == 'common.english')
            if (item.guideContent && item.title && this.guideStepList.length <= 7) {
                this.guideStepList.push({
                    attachTypeCode: FILE_TYPE.IMAGE,
                    contentList: JSON.parse(JSON.stringify(this.guideDetailsLangDefaultValue)),
                    fileName: this.fileInfo.imageName,
                    firstRegisterDate: '',
                    firstRegisterTellerID: await DataCenter.getData("employeeNo") ? await DataCenter.getData("employeeNo") : '',
                    firstRegisterTellerName: await DataCenter.getData("employeeName") ? await DataCenter.getData("employeeName") : '',
                    firstRegisterTime: '',
                    guideID: 0,
                    seqNo: this.guideStepList.length + 1,
                    statusCode: '01',
                    image: this.getImage === null ? '' : this.getImage,
                    isChecked: false,
                    imgSize: this.fileInfo.imgSize,
                    imagePreview: this.fileInfo.image,
                    title: '',
                    filePath: '',
                    fileId: '',
                    savedYN: 'N',
                    removeImageYN: 'N'
                })
                this.isInValidInput = false
                this.removeFile(FILE_TYPE.IMAGE)
                this.handleClear()
            } else if (!item.guideContent || !item.title) {
                this.isInValidInput = true
                this.$util.scrollToElement('inp_error')
            } else if (this.guideStepList.length > 7) {
                this.isExceed = true
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "delete":
                    if (this.$route.query.type === "edit") {
                        this.$dialog.onOpen({
                            content: this.$t('message.are_you_sure_you_want_to_delete'),
                            dialogMode: 'Question',
                            callback: () => {
                                this.handleDelete()
                                modalController.dismiss()
                            },
                        });
                    } else {
                        this.$router.push("ICM206001SC00");
                    }
                    break;
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: 'Question',
                        callback: () => {
                            if (this.$route.query.type === "edit") {
                                this.handleClear()
                                this.$util.editableContent(false)
                                this.isDisable = true
                                this.isInValidInput = false
                                this.isExceed = false
                                this.requestGuideDetail()
                                this.requestGuideStepDetailInquiryList()
                            } else {
                                this.$router.push("ICM206001SC00")
                            }
                            modalController.dismiss()
                        },
                    });
                    break;
                case "save":
                    this.$dialog.onOpen({
                        content: this.$t("message.would_you_like_to_save"),
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type === "edit") {
                                this.$util.editableContent(false)
                                this.handleUpdate()
                            } else {
                                this.handleRegister()
                            }
                            modalController.dismiss()
                        },
                    })
                    break;
                case "edit":
                    this.isDisable = false
                    this.$util.editableContent(true)
                    break;
                default:
                    this.$router.push("ICM206001SC00")
                    break;
            }
        },

        convertLangToCode(listsArr: ContentList[]): ContentList[] {
            const data = listsArr.map((item: ContentList) => {
                item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value")
                return item;
            });
            return data;
        },

        handleUploadImage(event: any) {
            this.isImageEmty = false
            const accectFile = /(\.png)$/i;
            const files = event.target.files;
            const file = files[0];
            if (!accectFile.exec(file.name)) {
                this.$dialog.onOpen({
                    content: this.$t('message.only_png_file_can_be_inserted'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else if (file.size > FILE.SIZE_2MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_2MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getImage = file;
                const size = fileSize.countFileSize(file.size)
                this.fileInfo.imgSize = size
                const reader = new FileReader()
                reader.readAsDataURL(file)
                this.fileInfo.imageName = file.name
                reader.onload = () => {
                    this.fileInfo.image = String(reader.result)
                };
            }
            const duplicatedImage = document.getElementById('btn02 btn_upload') as HTMLInputElement;
            duplicatedImage.value = ''
        },

        removeFile(attachTypeCode: string) {
            switch (attachTypeCode) {
                case FILE_TYPE.FILE:
                    this.isFileEmty = true
                    this.getFile = null
                    this.fileInfo.fileSize = this.fileInfo.fileName = ''
                    break;
                case FILE_TYPE.IMAGE:
                    this.isImageEmty = true
                    this.guideStepList.forEach((item) => {
                        if (item.isChecked) {
                            item.removeImageYN = 'Y'
                        }
                    })
                    this.getImage = null
                    this.fileInfo.imageName = this.fileInfo.imgSize = ''
                    break;
            }
        },

        async handleUploadFile(event: any) {
            this.isFileEmty = false
            const files = event.target.files
            const file = files[0]
            if (file.size > FILE.SIZE_10MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_10MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getFile = file
                const size = fileSize.countFileSize(file.size)
                this.fileInfo.fileSize = size
                const reader = new FileReader()
                reader.readAsDataURL(file)
                this.fileInfo.fileName = file.name
            }
            const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
            duplicatedFile.value = ''
        },

        async handleFileUpdate() {
            if (this.getFile === null && this.isFileEmty) {
                for (let i = 0; i < this.guideDetail.attachInfoList.length; i++) {
                    if (this.guideDetail.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
                        await this.$file.updateFileStatus(this.guideDetail.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
                        const index = this.guideDetail.attachInfoList.indexOf(this.guideDetail.attachInfoList[i]);
                        this.guideDetail.attachInfoList.splice(index, 1);
                    }
                }
            }
            if (this.getFile !== null && !this.isFileEmty) {
                if (this.guideDetail.attachInfoList.length === 0) {
                    const res = await this.$file.uploadFile([this.getFile], "ICM")
                    for (let i = 0; i < res.data.body.fileList.length; i++) {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.guideDetail.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.FILE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                } else if (this.guideDetail.attachInfoList.length > 0) {
                    if (this.isFileAlreadyExisted(FILE_TYPE.FILE)) {
                        const res = await this.$file.uploadFile([this.getFile], "ICM")
                        for (let index = 0; index < this.guideDetail.attachInfoList.length; index++) {
                            if (this.guideDetail.attachInfoList[index].attachTypeCode === FILE_TYPE.FILE) {
                                await this.$file.updateFileStatus(this.guideDetail.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                                for (let i = 0; i < res.data.body.fileList.length; i++) {
                                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                                        Object.assign(this.guideDetail.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.FILE, filePath: v.data.body.filePath })
                                    )
                                }
                            }
                        }
                    } else {
                        const res = await this.$file.uploadFile([this.getFile], "ICM")
                        for (let i = 0; i < res.data.body.fileList.length; i++) {
                            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                            this.guideDetail.attachInfoList.push({
                                filePath: resFileInfo.data.body.filePath,
                                attachTypeCode: FILE_TYPE.FILE,
                                fileId: res.data.body.fileList[i].fileId
                            })
                        }
                    }
                }
            }

            for (let i = 0; i < this.guideStepList.length; i++) {
                if (this.guideStepList[i].image?.length !== 0) {
                    const res = await this.$file.uploadFile([this.guideStepList[i].image], "ICM")
                    if (res.data.body) {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[0].fileId)
                        this.guideStepList[i].fileId = res.data.body.fileList[0].fileId
                        this.guideStepList[i].attachTypeCode = FILE_TYPE.IMAGE
                        this.guideStepList[i].filePath = resFileInfo.data.body.filePath
                    }
                }
                if (this.guideStepList[i].removeImageYN === 'Y') {
                    this.guideStepList[i].fileId = ''
                    this.guideStepList[i].attachTypeCode = ''
                    this.guideStepList[i].filePath = ''
                }
            }
        },

        async handleFileRegister() {
            if (this.getFile !== null) {
                const res = await this.$file.uploadFile([this.getFile], "ICM")
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                    const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                    if (resFileInfo) {
                        this.guideDetail.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.FILE,
                            fileId: resFileInfo.data.body.fileId
                        })
                    }

                }
            }

            for (let i = 0; i < this.guideStepList.length; i++) {
                if (this.guideStepList[i].image?.length !== 0) {
                    const res = await this.$file.uploadFile([this.guideStepList[i].image], "ICM")
                    const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[0].fileId)
                    this.guideStepList[i].fileId = res.data.body.fileList[0].fileId
                    this.guideStepList[i].attachTypeCode = FILE_TYPE.IMAGE
                    this.guideStepList[i].filePath = resFileInfo.data.body.filePath
                }
            }
        },

        isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
            const isFileAlreadyExisted = this.guideDetail.attachInfoList.some(obj => Object.values(obj).includes(fileContentType))
            return isFileAlreadyExisted
        },

        async handleCheckbox(indx: number) {
            for (let index = 0; index < this.guideStepList.length; index++) {
                if (indx === index) {
                    this.guideStepList[index].isChecked = !this.guideStepList[index].isChecked
                } else {
                    this.guideStepList[index].isChecked = false
                }
            }

            if (this.isItemChecked) {
                await this.convertFromTableToRegisterForm()
                this.$util.editableContent(false)
                this.isEditFormData = false
            } else {
                this.$util.editableContent(true)
                this.isEditFormData = true
            }
        },

        handleClear() {
            this.guideDetailsLangDefaultValue.forEach((item) => {
                item.guideContent = '',
                item.guideContentHtml = '<p><br/></p>',
                item.title = ''
            })
            this.getImage = null
            this.fileInfo.imageName = this.fileInfo.imgSize = this.fileInfo.image = ''
            for (let i = 0; i < this.guideDetailsLangDefaultValue.length; i++) {
                this.guideDetailsLangDefaultValue[i].guideContent = this.guideDetailsLangDefaultValue[i].title = ''
                this.guideDetailsLangDefaultValue[i].guideContentHtml = '<p><br/></p>'
            }
        },

        handleRemove() {
            this.guideStepList.forEach(item => {
                if (item.isChecked) {
                    item.statusCode = '09'
                }
            })
            this.isEditFormData = false
            this.isInValidInput = false
            this.isItemDeleted = true
        },

        handleFormEdit() {
            this.isGuideEdit = true
            this.isEditFormData = true
            this.$util.editableContent(true)
        },

        handleFormCancel() {
            this.$util.editableContent(false)
            this.isGuideEdit = false
            this.handleClear()
            this.convertFromTableToRegisterForm()
            this.isEditFormData = false
        },

        async handleFeeInfoSave() {
            const item: any = this.guideDetailsLangDefaultValue.find(data => data.languageCode == 'common.english')
            if (item.guideContent && item.title) {
                const obj = this.guideStepList.find(item => item.isChecked) as Guide_Step_Detail_Inquiry_List
                obj.firstRegisterTellerID = await DataCenter.getData("employeeNo") ? await DataCenter.getData("employeeNo") : ''
                obj.firstRegisterTellerName = await DataCenter.getData("employeeName") ? await DataCenter.getData("employeeName") : ''
                obj.contentList = JSON.parse(JSON.stringify(this.guideDetailsLangDefaultValue))
                obj.image = this.getImage === null ? [] : this.getImage
                obj.fileName = this.fileInfo.imageName
                obj.imgSize = this.fileInfo.fileSize
                obj.imagePreview = this.fileInfo.image
                obj.savedYN = 'Y'
                this.isGuideEdit = false
                this.isEditFormData = false
                this.isInValidInput = false
                this.$util.editableContent(false)
            } else {
                this.isInValidInput = true
                this.$util.scrollToElement('inp_error')
            }
        },

        async convertFromTableToRegisterForm() {
            const obj = JSON.parse(JSON.stringify(this.guideStepList.filter(item => item.isChecked) as Guide_Step_Detail_Inquiry_List[]))
            this.isItemDeleted = obj[0]?.statusCode === '09' ? true : false

            for (let i = 0; i < this.guideDetailsLangDefaultValue.length; i++) {
                this.guideDetailsLangDefaultValue.forEach((item: ContentList) => {
                    if (item.languageCode === obj[0]?.contentList[i]?.languageCode) {
                        item.languageCode = obj[0]?.contentList[i]?.languageCode
                        item.title = obj[0]?.contentList[i]?.title
                        item.guideContent = obj[0]?.contentList[i]?.guideContent
                        item.guideContentHtml = obj[0]?.contentList[i].guideContentHtml ? obj[0]?.contentList[i]?.guideContentHtml : '<p><br</p>'
                    }
                })
            }

            if (obj[0]?.fileId !== '' && obj[0]?.savedYN === 'N') {
                this.requestImageDetail(obj[0]?.fileId)
            } else {
                this.fileInfo.imageName = obj[0]?.fileName ? obj[0]?.fileName : ''
                this.fileInfo.imgSize = obj[0]?.imgSize ? obj[0]?.imgSize : ''
                this.fileInfo.image = obj[0]?.imagePreview ? obj[0]?.imagePreview : ''
            }
        },
        
        convertCommonCode(listArr: ContentList[]): ContentList[] {
            const convertLangCode = listArr.map((item: ContentList) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : ''
                return item;
            })
            return convertLangCode
        },
    },
});
